.topMenu {
  display: flex;
  justify-content: space-between;
  background-color: transparent;

  position: absolute;
  top: 0;
  z-index: 999;
  padding: 30px 20px;
  width: 100%;
  max-width: 1000px;
  align-self: center;
  .left {
    .iconHome {
      // background-color: black;
      img {
        width: fit-content;
        height: fit-content;
      }
    }
  }
  .right {
    display: flex;
    align-items: center;
    .links {
      display: none;
      ul {
        display: flex;
        list-style-type: none;
        color: #fff;

        li {
          margin: 0 10px;
        }
      }
    }
  }
}

@media screen and (min-width: 1440px) {
  .topMenu {
    padding: 30px 0;

    .links {
      display: flex !important;
      // margin-right: 40px;

      ul {
        padding: 0;
        li {
          margin: 0 20px;

          &:last-child {
            margin-right: 0;
          }

          a {
            color: unset;
            text-decoration: none;
            &:hover {
              text-decoration: underline;
            }
          }
        }
      }
    }

    .burger {
      display: none;
    }
  }
}
